.info-modal {
  width: 400px;
  padding: 0 12px 20px 12px;
  height: fit-content;
  .title {
    padding: 16px 0;
    font-size: 20px;
    text-align: center;
  }
  ul {
    display: flex;
    max-width: 400px;
    gap: 12px;
    justify-content: center;
    .is-reverse {
      img {
        transform: rotate(180deg);
      }
    }
    li {
      flex: 1 1 0 auto;
      max-width: 125px;
      height: 220px;
      img {
        width: 100%;
        height: 100%;
      }
    }
  }

  .tab {
    display: flex;
    margin-top: 20px;
    justify-content: space-around;
    .tab-nav {
      width: 100%;
      padding-bottom: 10px;
      text-align: center;
      color: #464646;
      cursor: pointer;
      &.is-select {
        font-weight: 600;
        border-bottom: 3px solid salmon;
      }
    }
  }

  .content {
    margin-top: 8px;
    .tab-content {
      display: none;
      &.is-select {
        display: block;
      }
    }
  }
}